.heading{
  background-color: blue;
  display: flex;
  height: 300px;
}
.logo img{
  height: 50px;
  width: 50px;
}
.content{
  padding-top: 50px;
  color: white;
  font-family: sans-serif;
}
.img2 img {
  padding: 50px;
  height: 200px;
  width: 200px;
}

.icon{
  justify-content: center;
  text-align: center;
  clear: both;
  padding: 5px 5px 5px 5px;
}
.icon button{
  color: blue;
  background-color: whitesmoke;
  border-radius: 5px;
}

.loginform{
  text-align: center;
  justify-content: center;
  background-color: white;
  height: 500px;
  width: 30%;
  margin-top: 20px;
  border-radius: 20px;
  box-shadow: 5px 5px 5px gray;
}
.logcontent{
  padding: 20px;
  margin: 20px;
}
.logcontent input{
  border-radius: 5px;
  height: 20px;
 
}
.submit{
  height: 30px;
  width: 90%;
  border-radius: 5px;
  background-color: blue;
  color: white;
}
.footer{
  padding: 10px 10px 10px 10px;
  margin: 10px;
}